<template>
  <v-app>
    <template v-if="!mounting">
      <template v-if="login">
        <navigation v-model="drawer"/>
        <v-app-bar
          app dark clipped-left
          class="black"
        >
          <v-app-bar-nav-icon
            @click="drawer = !drawer"
          ></v-app-bar-nav-icon>
          <v-toolbar-title class="d-flex">
            <v-img
              src="/logo.png"
              width="30" height="30"
              class="mr-2"
            />
            Magnifiscent500
          </v-toolbar-title>
          <v-spacer />
          <v-badge
            :content="12" :value="12"
            color="red" overlap
            class="mr-4"
          >
            <v-icon>
              fas fa-bell
            </v-icon>
          </v-badge>
          <v-menu
            bottom
            origin="center center"
            transition="scale-transition"
            :offset-y="true"
            nudge-bottom="10"
            min-width="200px"
          >
            <template v-slot:activator="{ on }">
              <v-btn v-on="on" class="ml-2 elevation-0" fab small>
                <v-avatar :class="roleColor(profile.role.name)" size="35">
                  <span v-if="profile.first_name && ! profile.avatar" class="white--text text-button">
                    {{ profile.first_name.substr(0, 1) + profile.last_name.substr(0, 1) }}
                  </span>
                  <img
                    v-else alt="avatar"
                    :class="borderColorByRole(profile.role.name)"
                    :src="profile.avatar"
                  />
                </v-avatar>
              </v-btn>
            </template>
            <v-card class="elevation-0 pa-0" dark>
              <v-card-text class="pa-0">
                <v-list dense rounded>
                  <v-list-item
                    to="/profile" link
                    active-class="custom-yellow"
                  >
                    <v-list-item-action>
                      <v-icon style="font-size: 20px;">fas fa-user-circle</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title
                        class="text-left"
                        style="font-size: 14px;"
                      >
                        Profile
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item
                    @click="logOut()"
                    link active-class="primary white--text"
                  >
                    <v-list-item-action>
                      <v-icon style="font-size: 20px;">fas fa-sign-out-alt</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title class="text-left" style="font-size: 14px;">
                        Log Out
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list>
              </v-card-text>
            </v-card>
          </v-menu>
        </v-app-bar>
      </template>
      <v-main class="grey darken-3">
        <v-container fluid class="h-full">
          <router-view></router-view>
        </v-container>
      </v-main>
      <v-snackbar
        v-model="snackbar"
        :timeout="snackbarTimeout"
        top right
      >
        <template v-slot:default>
          <div class="py-2 pl-2 d-flex align-center">
            <div class="pr-2">
              <v-icon large :color="snackbarType">
                fas {{ snackbarType === 'success' ? 'fa-smile-beam' : 'fa-frown' }}
              </v-icon>
            </div>
            <div>{{ snackbarText }}</div>
          </div>
        </template>
        <template v-slot:action="{ attrs }">
          <div class="pa-2">
            <v-btn
              @click="snackbar = false"
              color="primary" text fab v-bind="attrs" small
            >
              <v-icon small>fas fa-times</v-icon>
            </v-btn>
          </div>
        </template>
      </v-snackbar>
    </template>
    <page-loading v-else />
  </v-app>
</template>

<script>
import { mapGetters } from 'vuex'
import Navigation from '@/components/Navigation'
import PageLoading from '@/components/PageLoading'

export default {
  name: 'App',
  components: {
    Navigation,
    PageLoading
  },
  data: () => ({
    drawer: null,
    mounting: true
  }),
  computed: {
    ...mapGetters({
      login: 'login',
      profile: 'profile',
      role: 'role',
      snackbarText: 'snackbarText',
      snackbarType: 'snackbarType',
      snackbarTimeout: 'snackbarTimeout'
    }),
    snackbar: {
      get () {
        return this.$store.getters.snackbar
      },
      set (value) {
        this.$store.commit('setSnackbar', value)
      }
    }
  },
  async mounted () {
    if (this.login) {
      await this.$store.dispatch('getProfile')
      if (this.role === 'User') {
        await this.$store.dispatch('getAccounts')
      }
      this.mounting = false
    } else {
      this.mounting = false
    }
  },
  methods: {
    async logOut () {
      await this.$axios.post('/log-out')
      this.$cookies.keys().forEach(cookie => this.$cookies.remove(cookie))
      this.$store.commit('reset')
      this.$router.push('/login')
    },
    showProfile () {
      this.$router.push({path:'/user/profile'})
   
    }
  }
}
</script>

<style>
.yellow-brand-color {
  background-color: linear-gradient(90deg,#bf953f,#fcf6ba,#b38728,#fbf5b7,#aa771c);
}
</style>
